import { getArtistSublist } from '@/request/api/collect';
export default {
  data() {
    return {
      // 收藏的歌手数
      singerNum: 0,
      // 用户收藏的专辑(不包括数字专辑)
      collectSingers: []
    };
  },

  async mounted() {
    if (sessionStorage.getItem("cookie")) {
      // 先获取收藏的歌手数量
      const res = await getArtistSublist(sessionStorage.getItem("cookie"), this.singerNum);
      this.singerNum = res.data.count; // console.log('收藏的歌手数量', this.singerNum);
      // 再根据收藏数量去请求相应数量的数据

      const res1 = await getArtistSublist(sessionStorage.getItem("cookie"), this.singerNum); // console.log('获取收藏的歌手列表', res1);

      this.collectSingers = res1.data.data; // console.log('收藏的歌手列表', this.collectSingers);
    }
  },

  methods: {
    // 去往歌手页
    goSingerPage(id) {
      this.$router.push({
        path: '/SingerPage',
        query: {
          id: id
        }
      });
    }

  }
};